* {
    font-family: 'Rubik', sans-serif;
}

.w-100 {
    width: 100%;
}

/* Carousel items */

.ant-carousel {
    background-image: url("https://res.cloudinary.com/duz7rxjxk/image/upload/v1607745476/main-bg_c0bvtu.jpg");
    width: 100%;
    min-height: 580px;
    background-position: center;
    background-color: #fff;
    background-repeat: no-repeat;
    background-size: cover;
    box-shadow: 0 7px 30px -6px rgba(0,0,0,0.15);
    overflow: hidden;
    padding-top: 50px;
    margin-bottom: 50px;
}

.ant-carousel-vertical .slick-dots li {
    background-color: #000;
    width: 20px;
    height: 19px;
}

.ant-carousel-vertical .slick-dots li.slick-active {
    width: 25px;
    height: 19px;
    background-color: #33303073;
}

.ant-carousel-vertical .slick-dots li button {
    width: 24px;
    height: 16px;
}

.ant-carousel .align-items-center {
    -ms-flex-align: center !important;
    align-items: center !important;
}

.ant-carousel .justify-content-center {
    -ms-flex-pack: center !important;
    justify-content: center !important;
}


.ant-carousel .from-bottom {
    -webkit-transform: translateY(45px);
    -ms-transform: translateY(45px);
    transform: translateY(45px);
    transition: all .45s .3s ease-in-out;
    opacity: 0;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden
}

.slick-active .from-bottom {
    -webkit-transform: translateY(0);
    -ms-transform: translateY(0);
    transform: translateY(0);
    opacity: 1;
}

.ant-carousel .from-bottom img {
    text-align: left;
    width: 150px;
    margin-bottom: 20px;
}

.ant-carousel .from-bottom .text-body {
    font-size: 32px;
    font-weight: 300;
    line-height: 1.2;
}

.ant-carousel .from-bottom .text-price-medium {
    font-weight: 500;
}

.ant-carousel .from-bottom button {
    margin-top: 20px;
}

.slick-active .scale-up {
    -webkit-transform: scale(1) !important;
    -ms-transform: scale(1) !important;
    transform: scale(1) !important;
    opacity: 1 !important;;
}

.ant-carousel .from-bottom .scale-up {
    -webkit-transform: scale(1.2);
    -ms-transform: scale(1.2);
    transform: scale(1.2);
    transition-delay: .5s;
    opacity: 0;
    transition: all .45s .4s ease-in-out;
}

.ant-card-body {
    padding: 15px;
    text-align: center
}

.card_item {
    overflow: hidden;
    margin-bottom: 20px;
    border-radius: 5px;
}

.ant-card-bordered {
    height: 100%;
}

.card_item .ant-card-cover {
    height: 50%;
    overflow: hidden;
}

.ant-card-cover > * {
    width: 50%;
    max-width: 100%;
    margin: 10px auto;
}

.product-category {
    width: 100%;
    margin-bottom: 6px;
    font-size: 12px; 
    text-align: center; 
}

.product-category p{
    transition: color .2s;
    color: #999;
    text-decoration: none;
    text-transform: capitalize;
}

.product-category:hover p{
    color: #05f;
}

.product-card .product-category {
    transition: color .2s;
    color: #999;
    text-decoration: none;
}

.product-card .product-title {
    margin-bottom: 18px;
    font-size: 16px;
    font-weight: normal;
    text-align: center;
    height: 50px;
    -webkit-line-clamp: 2;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-box-orient: vertical;
}

.product-card .product-price {
    display: table;
    margin-bottom: 10px;
    padding: 9px 15px;
    border-radius: 4px;
    background-color: #fff6e3;
    color: #232323;
    font-size: 16px;
    font-weight: normal;
    text-align: center;
    align-items: center;
    justify-content: center;
    margin: 0 auto;
}

.product-card .ant-card-actions .anticon-heart svg{
    transition: all .45s .1s ease-in-out;
}

.product-card .ant-card-actions .anticon-heart:hover svg{
    fill: rgb(251, 99, 99);
}

.product-card .ant-card-cover {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px;
}

.item-modal-category span{
    display: inline-block;
    margin-right: 5px;
    color: #999;
    font-size: 13px;
    text-decoration: none;
    vertical-align: middle;
    transition: color .25s;
}

.ant-modal-body img {
    display: flex;
}

.ant-modal-body .ant-image {
    margin: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
}

.item-modal-category p{
    display: inline-block;
    margin-right: 3px;
    color: #999;
    font-size: 13px;
    text-decoration: none;
    transition: color .25s;
}

.item-modal-category p:hover {
    color: #05f;  
}

.item-modal-title {
    font-weight: 300;
    font-size: 22px;
}

.item-modal-price {
    font-weight: 300;
    font-size: 25px;
}

.item-text-muted {
    color: #999;
}

.btn-modal-cart{
    margin-left: 20px;
}

.btn-modal-share {
    margin-right: 10px;
}

.item-modal-footer {
    display: inline-block;
}

.footer-float-right {
    float: right;
}

.ant-layout-header .li-cart-context {
    display: flex;
    align-items: center;
}

.anticon-shopping-cart {
    font-size: 22px !important;
    color: rgba(255, 255, 255, 0.9);
    padding-right: 5px;
}

.cart-items-total {
    background: red;
    border-radius: 50%;
    padding: 5px 8px;
    color: white;
}

.float-cart{
    width: 28%;
    height: 100%;
    position: fixed;
    top: 0;
    right: -28%;
    z-index: 99;
    background-color: white;
    box-sizing: border-box;
    -webkit-transition: right .2s;
    transition: right .2s;
    padding: 1% 0;
    overflow-y: scroll;
}

.float-cart-open {
    right: 0;
}

.ant-layout-header {
    -webkit-transition: right .2s;
    transition: right .2s;
    right: 0;
    position: relative;
}

.navbar-open-cart {
    right: 28%;
    
}

.cart-item {
    color: #000;
    padding: 0 5%;
}

.cart-item__thumb {
    width: 100%;
    margin: 0 auto;
}

.cart-item .ant-divider-horizontal {
    margin: 10px 0;
}

.cart-item p {
    font-size: 14px;
}

.btn-actions-cart {
    float: right;
    border-radius: 0;
    display: inline-block;
}

.cart-item__price {
    float: right;
    margin: 8px 0;
}

.float-cart-footer {
    padding: 15px 2%;
    box-sizing: border-box;
    position: fixed;
    bottom: 0;
    width: 28%;
    z-index: 999;
    background-color: white;
}

.float-cart-footer:before {
    content: "";
    width: 100%;
    height: 20px;
    display: block;
    position: absolute;
    top: -20px;
    left: 0;
    background: -webkit-linear-gradient(bottom,rgba(0,0,0,.2),transparent);
    background: linear-gradient(0deg,rgba(0,0,0,.2),transparent);
}

.float-cart-footer__total {
    float: right;
}

.float-cart-footer button{
    width: 100%;
}

.float-cart-open .ant-empty{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
}

.float-cart__render_empty {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
}

.float-cart__render_empty span{
    color: #e4e8ec;
    font-size: 5rem !important;
    position: absolute;
    width: 100%;
}

.float-cart__render_empty p {
    color: #000000;
    position: absolute;
    font-size: 20px;
    align-items: center;
}

.select_sort_items {
    padding-right: 15px;
}

.select_sort_items .ant-select{
    width: 150px;
    float: right;
}

.show_total_items {
    padding-left: 15px;
}

.select_sort_items p{
    float: right;
    margin-right: 5px;
    padding-top: 5px;
}

.list-prices {
    list-style: none;
    padding-left: 0;
    margin-bottom: 5px;
}

#filter_prices_form_controls input{
    width: 95%;
}

.list-prices li {
    cursor: pointer;
    text-decoration: none;
    font-size: 15px;
}

.list-prices li:hover {
    text-decoration: underline;
}

.list-categories {
    list-style: none;
    padding-left: 0;
    margin-bottom: 5px;
}

.list-categories li {
    cursor: pointer;
    text-decoration: none;
    font-size: 15px;
}

.list-categories li:hover {
    text-decoration: underline;
}